import React from "react";

import { Box, Input } from "bloomer";

function TitleEditorCard(props) {
  return (
    <Box
      className="EventCard TitleEditorCard"
      style={{
        width: "25vw",
        padding: "0.75rem 0.5rem",
        border: "1px solid #d22820"
      }}
    >
      <div className="field is-horizontal">
        <div className="field-label is-normal">
          <label className="label">Title</label>
        </div>
        <div className="field-body">
          <div className="field">
            <p className="control">
              <Input
                type="text"
                value={props.emailTitle}
                onChange={e =>
                  props.updateTitleValue("emailTitle", e.target.value)
                }
              />
            </p>
          </div>
        </div>
      </div>
      <div className="field is-horizontal">
        <div className="field-label is-normal">
          <label className="label">Subtitle</label>
        </div>
        <div className="field-body">
          <div className="field">
            <p className="control">
              <Input
                type="text"
                value={props.emailSubtitle}
                onChange={e =>
                  props.updateTitleValue("emailSubtitle", e.target.value)
                }
              />
            </p>
          </div>
        </div>
      </div>
    </Box>
  );
}

export default TitleEditorCard;
