import React from "react";

import { Delete, Label, Field, Control, Input, Columns, Column } from "bloomer";

function DateGroup(props) {
  return (
    <div style={{ padding: "5px", marginBottom: "10px" }}>
      <Columns>
        <Column>
          <Field>
            <Label>Date</Label>
            <Control>
              <Input
                type="date"
                value={props.date}
                onChange={e =>
                  props.updateDateValue(
                    props.eventIndex,
                    props.dateIndex,
                    "date",
                    e.target.value
                  )
                }
              />
            </Control>
          </Field>
        </Column>
        <Column>
          <Field>
            <Label>Start Time</Label>
            <Control>
              <Input
                type="time"
                title="Type a to selct AM or p to select PM"
                value={props.startTime}
                onChange={e =>
                  props.updateDateValue(
                    props.eventIndex,
                    props.dateIndex,
                    "startTime",
                    e.target.value
                  )
                }
              />
            </Control>
          </Field>
        </Column>
        <Column>
          <Field>
            <Label>End Time</Label>
            <Control>
              <Input
                type="time"
                title="Type a to selct AM or p to select PM"
                value={props.endTime}
                onChange={e =>
                  props.updateDateValue(
                    props.eventIndex,
                    props.dateIndex,
                    "endTime",
                    e.target.value
                  )
                }
              />
            </Control>
          </Field>
        </Column>
        <Delete
          isSize="small"
          onClick={() =>
            props.decrementDates(props.eventIndex, props.dateIndex)
          }
        />
      </Columns>
    </div>
  );
}

export default DateGroup;
