import React from "react";

import {
  Box,
  Title,
  Delete,
  Label,
  Field,
  Control,
  Input,
  TextArea,
  Columns,
  Column,
  Button
} from "bloomer";

import DateGroup from "./DateGroup";

function EventCard(props) {
  return (
    <Box className="EventCard">
      <header>
        <Title isSize={4}>{props.title ? props.title : "Title"}</Title>
        <Button
          id="filler-button"
          isColor="danger"
          isOutlined
          onClick={() => props.toggleFillerContent(props.index)}
        >
          Show Filler Content: {props.showFillerContent ? "Yes" : "No"}
        </Button>
        &nbsp;
        <Delete onClick={() => props.deleteMe(props.index)} isSize="large" />
      </header>

      <Columns>
        <Column></Column>
      </Columns>

      <Columns>
        <Column>
          <Field>
            <Label>Title</Label>
            <Control>
              <Input
                type="text"
                value={props.title}
                onChange={e =>
                  props.updateValue(props.index, "title", e.target.value)
                }
              />
            </Control>
          </Field>
          <Field>
            <Label>Location</Label>
            <Control>
              <Input
                type="text"
                value={props.location}
                onChange={e =>
                  props.updateValue(props.index, "location", e.target.value)
                }
              />
            </Control>
          </Field>
        </Column>

        <Column>
          <Field>
            <Label>Image Link</Label>
            <Control>
              <Input
                type="url"
                value={props.imageURL}
                onChange={e =>
                  props.updateValue(props.index, "imageURL", e.target.value)
                }
                placeholder="https://i.imgur.com/PH6Yj0F.png"
              />
            </Control>
          </Field>
          <Field>
            <Label>Facebook Link</Label>
            <Control>
              <Input
                type="url"
                value={props.facebookURL}
                onChange={e =>
                  props.updateValue(props.index, "facebookURL", e.target.value)
                }
                placeholder="www.facebook.com/events/574206449784456"
              />
            </Control>
          </Field>
        </Column>

        <Column>
          <Field>
            <Label>Description</Label>
            <Control>
              <TextArea
                value={props.description}
                onChange={e =>
                  props.updateValue(props.index, "description", e.target.value)
                }
              />
            </Control>
          </Field>
        </Column>
      </Columns>

      <hr />

      {props.dates.map((date, index) => (
        <DateGroup
          {...date}
          updateDateValue={props.updateDateValue}
          decrementDates={props.decrementDates}
          eventIndex={props.index}
          dateIndex={index}
          key={index}
        />
      ))}
      <Button
        id="add-button"
        isColor="danger"
        isOutlined
        isSize="small"
        onClick={() => props.incrementDates(props.index)}
      >
        Add Date
      </Button>
    </Box>
  );
}

export default EventCard;
